    box-shadow: 2px 3px 3px 0px#0000009a;
    background-color: var(--background-light);
  }
  .card:hover::after {
    background-color: hsla(0, 0%, 100%, 0.437);
    /* background: linear-gradient(-90deg, rgba(255, 255, 255, 0.46), #fff0); */
    border-radius: 3px;
    content: "";
    display: block;
    left: -5%;
    min-width: 110%;
    min-height: 105%;

    position: absolute;
    top: -0.5rem;

    z-index: -1;
  }
  .cardPreview p {
    color: white;
    font-family: "Playfair";
    text-align: center;
  }

  .cardPreview .previewImg {
    max-height: 14rem;
    min-height: 14rem;
  }

  .cardPreview.green {
    background-color: rgb(36, 112, 0);
  }
  .cardPreview.yellow {
    background-color: rgb(223, 189, 0);
  }
  .cardPreview.red {
    background-color: rgb(153, 31, 0);
  }
  .cardPreview.orange {
    background-color: rgb(230, 122, 0);
  }
  .cardPreview.pink {
    background-color: rgb(184, 0, 190);
  }

  .cardHeading h3 {
    font-size: var(--card-heading-font-size-desktop);
  }
  .cardDetails p {
    display: block;
    min-height: 4rem;
  }
  .cardDetails {
    display: grid;
    grid-template-rows: 20% 1fr;
    margin: 0 1rem var(--card-bottom-margin-desktop) 1rem;
  }

  .projectLinks {
    width: 100%;
  }
  .projectLinks a {
    margin: 0.1rem;
    min-width: initial;
    padding: 1rem 0.1rem 0;
  }

  .projectLinks img {
    margin: initial;
    min-width: 1rem;
  }
  .projectLinks .github {
    margin-right: 0;
  }

  .cardDetails p {
    font-size: var(--card-description-font-size-desktop);
    margin: 0 var(--card-description-side-margin-desktop);
  }

  /*
  
   #footerContent SECTION DESKTOP 
   
   */
  #directContact img {
    height: 2rem;
    margin: 0.1rem 0.5rem;
  }
  #contact .contactLinks {
    /*    left: -0.2rem; */
    margin: 0;
    margin-top: clamp(50px, 10vw, 200px);
    position: relative;
  }
  #contact .contactLinks > ul li {
    margin: 0 1.25rem 0 0;
  }
  #directContact button:hover {
    background-color: blue;
    border: 2px solid black;
    border-radius: 7px;
  }
  #directContact button {
    margin-bottom: 0.2rem;
    left: -0.7rem;
  }
  #footerContent {
    width: 100%;
    max-width: 2500px;
    justify-self: center;
    /* background-image: var(--gradBgLight); */
    background-image: linear-gradient(90deg, #0d8cb3a3, #0d8cb3, #0d8cb385);
  }
  #footerContent {
  }
}

@media (hover: hover) {
  .projectLinks a:hover img {
    transform: translateY(-0.1rem) scale(1.02);
  }
  .contactLinks a:hover img {
    transform: translateY(-0.3rem) scale(1.02);
  }
  .projectLinks.unavailable img:hover {
    /* ::after: content: "?";
  font-size: 2rem;
  top: 1rem;
  display:block
  background-color: blue;
  min-width: 1rem;
  min-height: 1rem; */
    /*   padding: 0.5rem; */

    height: 0;
    background-color: rgba(167, 167, 167, 0.891);
    padding: 0.5rem;
  }
}
@media (hover: none) {
  .projectLinks.unavailable img {
    height: 0;
    background-color: rgba(167, 167, 167, 0.891);
    padding: 0.5rem;
  }
}
