 padding: 16rem 23rem;\n  position: absolute;\n  transform: rotate(-20deg) translateY(-12rem) translateX(-2rem) scale(120%);\n\n  z-index: -1;\n}\n#headerContent {\n  align-items: center;\n  display: flex;\n  flex-direction: column;\n  padding-bottom: 2.8rem;\n}\n#headerContent h1 {\n  color: rgb(255, 255, 255);\n  font-size: var(--header-h1-font-size-mobile);\n  font-weight: 500;\n\n  position: absolute;\n  /*  bottom: calc(var(--headerHeight) + 2.5rem); */\n  left: 0;\n  right: 0;\n  margin: 0 auto;\n  max-width: min-content;\n  /*  top: calc(var(--headerHeight) - 8rem + 1vw); */\n  align-self: flex-end;\n  margin-bottom: 1.9rem;\n  text-align: start;\n  text-shadow: 2px 1px rgba(128, 128, 128, 0.742);\n}\n.left {\n  display: flex;\n}\n\n.left::before {\n  /* background: linear-gradient(\n    rgb(29, 109, 155),\n    rgb(235, 193, 9),\n    rgb(13, 140, 179)\n  ); */\n  /* background-color: #c9af44; */\n  /* background: linear-gradient(\n    white,\n    white,\n    white,\n    #e2c044d5,\n    #e2c044,\n    #7dde92,\n    #4e4187\n  ); */\n  /* background: linear-gradient(#b1d4ff 4px, #fff, #f1ffe7); */ /* f1ffe7 */\n  content: \"\";\n  left: -10px;\n  height: 100%;\n  width: 110%;\n  position: absolute;\n  z-index: -2;\n}\n#headshot {\n  margin: 1.7rem;\n  max-width: 87vw;\n  min-height: var(--headerHeight);\n\n  object-fit: cover;\n}\n.aboutHeading h2 {\n  text-align: center;\n}\n.about {\n  color: #616161;\n  font-size: var(--aboutMe-text-font-size-mobile);\n  letter-spacing: 0.007rem;\n  line-height: var(--aboutMe-line-height-mobile);\n  padding: 1rem 1.5rem;\n}\n#headerContent .contactLinks {\n  padding-top: 1rem;\n}\n.contactLinks > ul {\n  display: flex;\n  justify-content: center;\n  list-style: none;\n}\n.contactLinks > ul img {\n  margin: 0 1rem;\n  /* max- */\n  width: 1.8rem;\n}\n/*\n\n MAIN SECTION MOBILE\n \n */\n\n#main {\n  /*   padding: 2rem 0; */\n  background-color: rgb(255, 255, 255);\n  padding: clamp(5px, 5vw, 30px);\n}\n.workHeading {\n  /* should be \"about me\" h2, body: not(header) h2*/\n\n  color: black;\n  font-size: var(--workHeadingSize-mobile);\n\n  margin-top: 2rem;\n  position: relative;\n  text-align: center;\n}\n.card {\n  box-shadow: 1px 2px 3px 2px rgb(204, 204, 204);\n}\n.card:hover {\n  box-shadow: 1px 2px 5px 2px rgba(28, 28, 28, 0.49);\n}\n.cardPreview {\n  background-color: rgb(134, 134, 231);\n  display: flex;\n  justify-content: center;\n  margin: 0;\n\n  padding: 0;\n}\n.cardPreview p {\n  color: white;\n  font-family: \"Playfair\";\n  text-align: center;\n}\n.cardPreview .previewImg {\n  max-height: 18.2rem;\n  min-height: 18.2rem;\n\n  object-fit: cover;\n  width: 100%;\n}\n.cardPreview .previewImg.fill {\n  object-fit: fill;\n}\n\n.cardDetails {\n  display: grid;\n  grid-template-rows: 20% 1fr;\n  margin: 0 1rem 3.5rem 1rem;\n  padding-bottom: 3rem;\n  min-height: 9rem;\n}\n.cardHeading {\n  display: flex;\n  flex-wrap: nowrap;\n  grid-row: 1 / 2;\n\n  justify-content: space-between;\n\n  min-height: 3rem;\n}\n.projectLinks.unavailable img {\n  /* display: none; */\n}\n\n.projectLinks img {\n  align-self: center;\n  border-radius: 5px;\n\n  min-width: 1.2rem;\n  /*   margin: 0.2rem auto; */\n  transition: transform 0.2s ease;\n}\n.projectLinks a {\n  display: block;\n  margin-top: 0rem; /* \n  min-width: 2.4rem; */\n\n  padding: 0.8rem 0.5rem 1rem;\n}\n\n.projectLinks {\n  /*  max-width: 100px;*/\n  display: flex;\n  justify-content: end;\n  margin-right: -0.5rem;\n}\n\n.cardDetails p {\n  align-self: flex-start;\n  color: rgb(104, 104, 104);\n  grid-row: 2 / 3;\n  margin: 0 0.5rem;\n  padding-top: 1rem;\n  margin-top: 1rem;\n  font-size: var(--card-description-font-size-mobile);\n}\n.cardHeading h3 {\n  align-self: center;\n  font-family: \"Playfair\";\n  font-size: var(--card-heading-font-size-mobile);\n  font-weight: 300;\n  /* margin: .7rem 5rem 0 2rem; */\n  /* margin: .7rem 0rem 1rem 1rem; */\n  /*   min-width: 50%; */\n  text-wrap: nowrap;\n}\n\n/*\n\n #footerContent SECTION